﻿@charset "UTF-8";
$bfsize:10px;

div.aside {
    width: 100%;
    border: 1px solid #ddd;
    padding: 1*$bfsize;
    display: flex;
    flex-direction: column;
    margin-bottom: 2*$bfsize;

    >.aside-title {
        color: #7c7c7c;

        font: {
            size: 1.8*$bfsize;
        }
    }

    >div.content {
        padding-left: 0;

        ul {
            padding-left: 0;
            list-style: none;
        }
    }

    >div.content {
        ul {
            margin-bottom: 0;

            >li:last-child {
                margin-bottom: 0;
            }
        }
    }

    >div.content.number>ul {
        list-style-type: decimal;



        >li {
            font-size: 14px;
        }

    }
}