@charset "UTF-8";
@import "../components/_essay-index-aside.scss";
$bfsize:10px;
$leftWidth:310px;

@mixin defaulta {
    a {
        text-decoration: none;

        &:link {
            color: #000;
        }

        &:visited {
            color: #555;
        }

        &:hover {
            color: #337ab7;
        }
    }
}

.essay-main {
    width: 100%;
    padding: 0;
    padding-top: 1*$bfsize;
    margin: 0;
    overflow-x: hidden !important;
}

#bIContainer {
    padding: 0;
    padding-right: 0.8*$bfsize;
    margin: 0;
    border: none;
}

/* 小于769px */
@media (max-width: 767.8px) {
   
    .blogInfo > .b-left {
        display: none;
    }

    #bIContainer {
        padding-right: 0;
    }
}

/* End blog信息div样式 */
.blogInfo {
    margin-bottom: 2.4*$bfsize;
    box-shadow: 0 0.2*$bfsize 1*$bfsize 0 rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    padding-left: 1.6*$bfsize;
    padding-right: 1.6*$bfsize;
    padding-bottom: 1.6*$bfsize;
    padding-top: 1.6*$bfsize;
    background-color: #fff;
}



.blogInfo {
    display: flex;
    align-content: start;

    .b-left {
        @media (min-width: 768px) {
            display: flex;
            align-items: center;
        }

        margin-right: 1.6*$bfsize;
        border: 1px solid #efefef;

        > img {
            width: 200px;
            height: 145px;
        }
    }

    .b-right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        > h3 {
            margin-top: 0;
            margin-bottom: 1.4*$bfsize;
            font-size: 2*$bfsize;

            @include defaulta;

            .badge-sticky {
                margin-left: 4px;
                font-size: 10px;
            }
        }

        > p {
            margin-bottom: 1.4*$bfsize;
        }

        > .sapnContainer {
            padding: 0;
            margin: 0;
            border: none;
            display: flex;
            align-items: flex-end;
            flex-wrap: wrap;
            min-height: 48px;

            .fa {
                line-height: 2.4*$bfsize;
                font-size: 1.4*$bfsize;
                margin-right: 1*$bfsize;

                &:before {
                    padding-right: 0.4*$bfsize;
                }
            }

            > .pull-right {
                margin-right: 1*$bfsize;
            }
        }
    }
}

/* End blog信息div样式 */
